<template>
	<div class="song-wrap" ref="sw">
		<div class="song-detail">
			<div class="shead">
				<div v-show="isd" class="down" @click="closed"><i class="el-icon-arrow-down"></i></div>
				<div class="player">
					<img src="data:image/png;base64," class="play-bar-support">
					<img class="play-tiao amt" :class="{on:isPlay}" src="~assets/images/play_bn/play-bar.png"/>
					<div class="pan on" :class="{stop : !isPlay}">
						<img :src="imgSrc">
					</div>
				</div>
				<div class="lyric">
					<div class="tit">{{name}}</div>
					<div class="wd">歌手：<span>{{aName}}</span></div>
					<div class="lyric-wp" v-show='lrc.indexOf("[") > -1'>
						<div class="lyc-in amt" ref="lyn">
							<p class="amt" v-for="(item,index) in lrcfmt" :class="{on : curT === index}" >
								{{item.txt}}
							</p> 
						</div>
					</div>
					<div class="nolyc" v-show='lrc.indexOf("[") === -1'>暂无歌词</div>
				</div>
			</div>
			<div class="sd-con">
				<div class="comment">
					<comments :hot='hot' :comments="comments" :cnum="cnum"></comments>
					<pagination :pageSize='pageSize' :total="cnum" @goPage='goPage'></pagination>
				</div>
				<div class="sc-right">
					
				</div>
			</div>
		</div>
	</div>
	
</template>

<script>
	import Comments from 'components/content/Comments/Comments.vue'
	import Pagination from 'components/content/Pagination/Pagination.vue'
	import { getMusicComment } from 'network/find.js'
	
	
	export default{
		name:"SongDetail",
		data(){
			return {
				isPlay:false,
				isd:false,
				hot:[],
				comments:[],
				cnum:0,
				pageSize:20,
				page:0,
				lrcfmt:[],
				curTime:999,
				duration:10,
				curT:0,
				timer:null,
				pheight:30,
				lrc:''
				
			}
		},
		created() {
			
			
		},
		watch:{
			lrc(){
				this.getComment();
				this.ph = 0;
				this.$refs.sw.scrollTop = 0;
			}
		},
		components:{
			Comments,
			Pagination
		},
		props:{
			id:{
				type:Number,
				default(){
					return 0
				}
			},
			name:{
				type:String,
				default(){
					return ""
				}
			},
			aName:{
				type:String,
				default(){
					return ""
				}
			},
			img:{
				type:String,
				default(){
					return ""
				}
			}
			
		},
		computed:{
			
			imgSrc(){
				return this.img + "?param=400y400"
			}
		},
		methods:{
			closed(){
				this.isd = false;
				this.$emit("closed")
			},
			goPage(val){
				this.page = val-1;
				this.getComment();
				this.$refs.sw.scrollTop = 560
			},
			getComment(){
				
				var pg = this.pageSize * this.page;
				getMusicComment(this.id, this.pageSize, pg).then(res => {
					this.hot = res.hotComments
					this.comments = res.comments
					this.cnum = res.total;
				})
			},
			lrcShow(){
				if(this.lrc.indexOf("[") === -1) {
					this.lrcfmt =[];
				}else{
					
				
					let arr = []
					let lrcArr = this.lrc.split("\n");
					lrcArr.pop();
					if(lrcArr[0].indexOf("[") === -1) {
						lrcArr.shift()
					}
					for(let item of lrcArr){
						
						
						let time = item.split("]")[0]
						time = time.split("[")[1]
						let txt = item.split("]")[1]
						
						let m = parseInt(time.split(":")[0])*60;
						let s = parseInt(time.split(":")[1].split(".")[0])
						let ss = parseInt(time.split(":")[1].split(".")[1])/1000
						
						time = m + s + ss;
						
						arr.push({time:time,txt:txt})
						
					}
					arr=arr.filter((ele, index, arr) => {
						return ele.txt!=""
					})
					
					arr.push({time:'9999999999',txt:" "})
					this.lrcfmt = arr;
				}
			},
			timePlay(){
				
				
				this.timer = setInterval(() => {
					for(let i=0; i<this.lrcfmt.length; i++){
						if(this.curTime>=this.lrcfmt[i].time && this.curTime<this.lrcfmt[i+1].time){
							if(i < 7){
								
								this.$refs.lyn.style.marginTop = 0;
							}else if(i>=7 && i<this.lrcfmt.length-7){
							
								let hh = 0;
								for(let j = 0; j < i-7; j++){
									hh += document.querySelectorAll(".lyc-in p")[j].offsetHeight;
								}
								
								this.$refs.lyn.style.marginTop = (-hh) + "px";
								
							}else{
								let hh = 0;
								for(let j = 0; j < this.lrcfmt.length-14; j++){
									hh += document.querySelectorAll(".lyc-in p")[j].offsetHeight;
								}
								this.$refs.lyn.style.marginTop = (-hh) + "px";
							}
							this.curT = i;
							
						}
					}
					
					if(this.curTime>=this.duration){
						clearInterval(this.timer)
					}
					
				},200)
			},
			timeStop(){
				if(this.timer !== null){
					clearInterval(this.timer)
				}
				
			}
		}
	}
</script>

<style lang="scss" scoped>
	.song-wrap{
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 70px;
		overflow: auto;
	}
	.song-detail{
		width: 1200px;
		margin: 0 auto;
	}
	.shead{
		display: flex;
	}
	.player{
		width: 550px;
		height: 550px;
		position: relative;
	}
	.play-bar-support{
		position: absolute;
		width: 35px;
		height: 35px;
		left: 226px;
		top: -10px;
		z-index: 2;
	}
	.play-tiao{
		position: absolute;
		left: 237px;
		top: 10px;
		width: 130px;
		transform-origin:left top;
		transform: rotate(-37deg);
		z-index: 6;
	}
	.play-tiao.on{
		transform: rotate(0deg);
	}
	.pan{
		width: 380px;
		height:380px;
		border-radius: 50%;
		border:13px solid #e6e5e6;
		background: #0f0f12;
		margin-left: 50px;
		margin-top: 100px;
		text-align: center;
	}
	.pan img{
		width: 260px;
		height: 260px;
		border-radius: 50%;
		margin-top: 47px;
	}
	.down{
		position: fixed;
		left: 15px;
		top: 15px;
		line-height: 30px;
		font-size: 16px;
		width: 30px;
		border-radius: 50%;
		cursor: pointer;
		text-align: center;
		font-size: 22px;
	}
	.down:hover{
		background: #eee;
	}
	.pan.on{
		animation: go 15s linear infinite;
	}
	.pan.stop{
		animation-play-state:paused;
	}
	@keyframes go {
	    from {   /*0%*/
	        transform: rotate(0deg);
	    }
	    to {    /*100%*/
	        transform: rotate(360deg);
	    }
	}
	.lyric{
		width: 600px;
		margin-top: 100px;
	}
	.tit{
		font-size: 26px;
		padding-left: 20px;
	}
	.wd{
		margin-top: 15px;
		font-size: 14px;
		padding-left: 20px;
		span{
			color: #517eaf;
		}
	}
	
	.comment{
		margin-top: 30px;
	}
	.lyric-wp{
		height: 458px;
		overflow: hidden;
		margin: 40px 0;
		padding-left: 20px;
	}
	.lyc-in{
		
	}
	.lyc-in p{
		font-size: 15px;
		color: $black3;
		line-height: 30px;
		padding-right: 100px;
	}
	.lyc-in p.on{
		font-weight: bold;
		color: $black1;
		animation: lyc 0.6s ease-out 1;
		transform-origin:left center;
		animation-fill-mode:forwards;
		transform:  scale(1.15); 
		line-height: 1.5;
		padding-top: 10px;
		padding-bottom: 10px;
	}
	@keyframes lyc {
	    0% {   
				
				color: #fff;
				letter-spacing: 1.3;
	      transform: scale(0.8); 
				opacity:0.5;
				filter: blur(3px);
	    }
			60%{
			  color:$navActiveColor;        
				filter: blur(0);		 
				transform: scale(1.23); 
			} 
	    100% {    
				letter-spacing: 1;
				color: $black1;
	      transform:  scale(1.15); 
				opacity:1;
				
	    }
	}
	.nolyc{
		padding: 30px 20px;
		font-size: 15px;
		color: $black3;
	}
</style>
